"use client";

import React, { ReactNode, useState, useEffect } from "react";
import TopBar from "./dashboard-components/TopBar";
import FooterDashboard from "./dashboard-components/FooterDashboard";
import Sidebar from "./dashboard-components/Sidebar";

interface DashboardLayoutProps {
  children: ReactNode;
}

const DashboardLayout: React.FC<DashboardLayoutProps> = ({ children }) => {
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const [isMobile, setIsMobile] = useState(false);

  // Handle responsive behavior
  useEffect(() => {
    const checkMobile = () => {
      const mobile = window.innerWidth < 1024;
      setIsMobile(mobile);
      
      // Auto-close sidebar on mobile, open on desktop
      if (mobile) {
        setSidebarOpen(false);
      } else {
        setSidebarOpen(true);
      }
    };

    // Initial check
    checkMobile();

    // Add event listener for window resize
    window.addEventListener('resize', checkMobile);
    
    // Cleanup
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
  };

  const closeSidebar = () => {
    if (isMobile) {
      setSidebarOpen(false);
    }
  };

  return (
    <div className="flex h-screen bg-gray-50 overflow-hidden">
      {/* Sidebar */}
      <Sidebar isOpen={sidebarOpen} onToggle={toggleSidebar} />
      
      {/* Main Content Area */}
      <div className="flex-1 flex flex-col min-w-0">
        {/* TopBar */}
        <TopBar onToggleSidebar={toggleSidebar} isSidebarOpen={sidebarOpen} />
        
        {/* Page Content - with click handler to close sidebar on mobile */}
        <div 
          className="flex-1 overflow-y-auto"
          onClick={closeSidebar}
        >
          <div className="p-6">
            {children}
          </div>
        </div>
        
        {/* Footer */}
        <FooterDashboard />
      </div>
    </div>
  );
};

export default DashboardLayout;