"use client";

import { Bell, Briefcase, Search, Settings } from "lucide-react";
import React from "react";

const TopBar = () => {
  return (
    <div className="w-full min-h-[60px] flex px-4 py-3 items-center text-white justify-between bg-[#1b2a49]">
      <div className="flex items-center gap-3">
        <span className="w-12 flex items-center justify-center h-12 rounded-lg bg-[#1f4c78] p-2">
          <Briefcase className="w-6 h-6" />
        </span>
        <div>
          <h1 className="text-2xl font-bold">BezMate-AI</h1>
          <h4 className="text-gray-200 text-sm">Farhan Trading LLC</h4>
        </div>
      </div>
      <div className="flex items-center gap-4">
        <div className="relative text-[#1B2A49]">
          <Search
            size={16}
            className="absolute top-1/2 -translate-y-1/2 left-3"
          />
          <input
            type="text"
            placeholder="Search invoices , Clients..."
            className="w-64 pl-10 pr-4 py-2 text-sm border bg-[#F4F7FA] border-[#E1E8F5] rounded-lg focus:outline-none focus:ring-1 focus:ring-[#1f4c78]"
          />
        </div>
        <div className="relative flex items-center justify-center w-10 h-10 rounded-lg bg-transparent transition hover:bg-[#1f4c78]">
          <Bell size={22} />
          <span className="absolute top-0 right-1 text-sm w-4 h-4 rounded-full bg-red-500 flex items-center justify-center">
            3
          </span>
        </div>
        <div className="flex items-center justify-center w-10 h-10 rounded-lg bg-transparent transition hover:bg-[#1f4c78]">
          <Settings size={22} />
        </div>
        <div className="flex items-center gap-2">
          <div className="w-12 h-12 rounded-full flex items-center justify-center text-lg font-semibold bg-[#1f4c78]">
            FA
          </div>
          <div>
            <h1 className="text-md font-semibold">Farhan Amjad</h1>
            <h2 className="text-sm text-gray-200">Owner</h2>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TopBar;
